::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  scrollbar-color: #3b71ca #f5f5f5;
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  background-color: #3b71ca;
}

.cursor-pointer {
  cursor: pointer;
}

.cursor-text {
  cursor: text;
}

.transition-all {
  transition: all 0.5s;
}

