.mission-content {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 550px;
  background-color: #7f0404;
}

.mission-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 410px;
  height: 500px;
  border: 1px solid black;

  background-color: green;
}

.mission-card {
  width: 350px;
  height: 450px;
  background-color: white;
  border-radius: 50px;
}

.mission-card::before {
  content: "";
  position: absolute;
  width: 350px;
  height: 40px;
  border-top-right-radius: 50px;
  border-top-left-radius: 50px;
  background-color: red;
}

.mission-cardBody {
  position: absolute;
  width: 350px;
  height: 410px;
  padding: 15px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  background-color: pink;
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
}

.line-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 410px;
  height: 39.5vh;
  border-bottom: 4px solid #000; /* Adds a bottom border */
  border-left: 4px solid #000; /* Adds a left border */
  border-right: 4px solid #000; /* Adds a right border */
  border-top: none;
  border-radius: 50px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.line-bottom::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 7vw;
  border-top: 4px solid black;
}

.line-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 410px;
  height: 30.5vh;
  border-top: 4px solid #000; /* Adds a bottom border */
  border-left: 4px solid #000; /* Adds a left border */
  border-right: 4px solid #000; /* Adds a right border */
  border-bottom: none;
  border-radius: 50px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background-color: transparent;
}

.line-top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 7vw;
  border-top: 4px solid black;
  -webkit-transform: translate(0, 29.5vh);
          transform: translate(0, 29.5vh);
}

.mission-circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 35px;
  height: 35px;
  border: 4px solid black;
  border-radius: 50%;
  background-color: transparent;
  -webkit-transform: translate(-1.2vw, 28vh);
          transform: translate(-1.2vw, 28vh);
}

.mission-dot-1 {
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background-color: black;
  border-radius: 50%;
  -webkit-transform: translate(7vw, 29.3vh);
          transform: translate(7vw, 29.3vh);
}

.mission-dot-2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background-color: black;
  border-radius: 50%;
  -webkit-transform: translate(0.5vw, 29.3vh);
